<template>
    <div class="collection">
    <input v-if="Object.keys(selectedItem).length === 0" ref="dropdowninput" v-model.trim="inputValue" class="dropdown-input" type="text" placeholder="Collection" spellcheck="false" autocomplete="off"/>
    <div v-else @click="resetSelection" class="dropdown-selected">
      <img :src="selectedItem[1][Object.keys(selectedItem[1])[0]][1]" class="item-img" onerror="this.onerror=null;this.src='https://via.placeholder.com/150/d1d1d1/?text=%20';"/>
          <span class="searchtext">{{selectedItem[0]}}<img src="https://img.icons8.com/plumpy/24/000000/verified-account.png" v-show="selectedItem[1][Object.keys(selectedItem[1])[0]][2]"/></span>
    </div>
    </div>
    <div class="dropdown">
    <div v-show="inputValue && apiLoaded" class="dropdown-list">
      <div @click="selectItem(item)" v-for="item in Object.fromEntries(Object.entries(visibleItemList).slice(0, 10))" :key="item" class="dropdown-item">
        <img :src="item[1][Object.keys(item[1])[0]][1]" class="dropdown-item-img" onerror="this.onerror=null;this.src='https://via.placeholder.com/150/d1d1d1/?text=%20';"/>
        <div class="listedtext">
        {{item[0]}}<img src="https://img.icons8.com/plumpy/24/000000/verified-account.png" v-show="item[1][Object.keys(item[1])[0]][2]"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      selectedItem: {},  
      inputValue: '',
      itemList: {},
      apiLoaded: false,
      apiUrl: '/getcollections',
    }
  },
  mounted () {
    this.getList()
  },
  methods: {
    resetSelection () {
      this.selectedItem = {}
      this.$nextTick( () => this.$refs.dropdowninput.focus() )
      this.$emit('on-item-reset')
    },
    selectItem (theItem) {
      this.selectedItem = theItem
      this.inputValue = ''
      this.$emit('on-item-selected', this.selectedItem)
    },
    itemVisible (item) {
      if (!this.inputValue){
        return false
      }
      let currentName = item[0].toLowerCase()
      let currentInput = this.inputValue.toLowerCase()
      return currentName.includes(currentInput)
    },
    getList () {
      axios.get(this.apiUrl).then( response => {
        this.itemList = response.data
        this.apiLoaded = true
      })
    }
  },
  computed: {
    visibleItemList: function() {
      return Object.entries(this.itemList).filter(this.itemVisible)
    } 
  }
}
</script>

