  vertical-align: top;
  display: inline-block;
  text-align: center;
  font-weight: bold;
  cursor: pointer;
  background: #fff;
  border-color: #e2e8f0;
  padding: 0rem;
  height: 7.5rem;
}
.dropdown-list{
  position: absolute;
  width: 102%;
  max-height: 20rem;
  margin-top: 4px;
  overflow-y: auto;
  background: #ffffff;
  box-shadow: 0 20px 20px rgba(0, 0, 0, 0.1), 0px 0px 50px rgba(0, 0, 0, 0.1);
  border-radius: 2rem;
  margin-left: -0.24rem;
  
}
.dropdown-item{
  display: flex;
  align-items: center;
  width: 100%;
  padding: 2rem 1rem;
  cursor: pointer;
  height: 3.5rem;
}
.dropdown-item:hover{
  background: #edf2f7;
}
