<script>
import solanaForm from './components/solanaForm.vue';
import { getLedgerWallet, getPhantomWallet, getSlopeWallet, getSolflareWallet, getSolletExtensionWallet, getSolletWallet, getCoin98Wallet } from '@solana/wallet-adapter-wallets'
import { WalletProvider } from '@solana/wallet-adapter-vue'
import { WalletMultiButton, WalletModalProvider } from '@solana/wallet-adapter-vue-ui'
import WorkspaceProvider from '@/components/WorkspaceProvider'


export default {
  setup(){
    const wallets = [
      getPhantomWallet(),
      getLedgerWallet(),
      getSolletWallet(),
      getSlopeWallet(),
      getSolflareWallet(),
      getSolletExtensionWallet(),
      getCoin98Wallet()
    ]
    return {
      wallets,
    }
  },
  data() {
    return {
      loading: false,
    }
  },
  components: {
      solanaForm,
      WalletProvider,
      WalletMultiButton,
      WalletModalProvider,
      WorkspaceProvider,
  }
}
