<template>
<a href="/" class="logo"><img src="./assets/logo.png"/></a>
    <wallet-provider :wallets="this.wallets" auto-connect v-if="!this.loading">
      <workspace-provider>
      <div id="wallet-button">
          <wallet-modal-provider logo="./assets/logo.png">
