          </wallet-modal-provider>
        </div>
        <div class="card">
            <solanaForm @loading="this.loading = true"></solanaForm>
        </div>
      </workspace-provider>
    </wallet-provider>
    <div v-if="this.loading" class="loadingcard">
       <div class="loader">Loading...</div>
       <div class="loadingmes">fetching data from marketplaces and rarity sites<br>this could take a minute</div>
    </div>
    <a href="https://discord.gg/9JQhG7GCr8" class="discord"><img src="./assets/Discord-Logo-Black.svg"/></a>
