<template>
    <form class="info" autocomplete="off" @submit.prevent="submitForm" action="/getresults" method="post" ref="form">
                <!--<input type="text" name="collection" id="collection" placeholder="Collection" spellcheck="false" autocomplete="off" @input="e => collection = e.target.value">-->
                <DropdownList @on-item-selected="collection = $event[0]; isCollection = true; markets = $event[1]" @on-item-reset="collection = ''; isCollection = false; markets={}"/>
                <input type="hidden" name="collection" :value="collection">
                <h3>Marketplace:</h3>
                <div class="marketplaces">
                    <input class="radio" id="rb1" type="radio" name="marketplace" :disabled="!marketplaces" value="all" v-model="marketplace">
                    <label class="noselect" for="rb1">All</label>
                    <input class="radio" id="rb2" type="radio" name="marketplace" :disabled="!solanart" value="solanart" v-model="marketplace">
                    <label class="noselect" for="rb2">Solanart</label>
                    <input class="radio" id="rb3" type="radio" name="marketplace" :disabled="!alphaArt" value="alphaArt" v-model="marketplace">
                    <label class="noselect" for="rb3">Alpha Art</label>
                    <input class="radio" id="rb4" type="radio" name="marketplace" :disabled="!magicEden" value="magicEden" v-model="marketplace">
                    <label class="noselect" for="rb4">Magic Eden</label>
                    <input class="radio" id="rb5" type="radio" name="marketplace" :disabled="!digitalEyes" value="digitalEyes" v-model="marketplace">
                    <label class="noselect" for="rb5">Digital Eyes</label>
                </div>
                <input type="hidden" name="marketops" :value="marketops">
                <h3>Rarity site:</h3>
                <div class="rankingsites">
                    <input id="rb6" type="radio" name="raritysite" :disabled="!raritysites" value="auto" v-model="raritysite">
                    <label class="noselect" for="rb6">Auto</label>
                    <input id="rb7" type="radio" name="raritysite" :disabled="!howrareis" value="howrareis" v-model="raritysite">
                    <label class="noselect" for="rb7">howrareis</label>
                    <input id="rb8" type="radio" name="raritysite" :disabled="!moonrank" value="moonrank" v-model="raritysite">
                    <label class="noselect" for="rb8">moonrank</label>
                    <input id="rb9" type="radio" name="raritysite" :disabled="!raritymon" value="raritymon" v-model="raritysite">
                    <label class="noselect" for="rb9">raritymon</label>
                </div>
                <h3>Donation:</h3>
                <div class="donations">
                    <input id="rb10" type="radio" name="donate" :disabled="!(raritysites && marketplaces)" value="0" v-model="donate">
                    <label class="noselect" for="rb10">Free</label>
                    <input id="rb11" type="radio" name="donate" :disabled="!(raritysites && marketplaces && this.iswallet)" value="0.005" v-model="donate">
                    <label class="noselect" for="rb11">0.005</label>
                    <input id="rb12" type="radio" name="donate" :disabled="!(raritysites && marketplaces && this.iswallet)" value="0.1" v-model="donate">
                    <label class="noselect" for="rb12">0.01</label>
                    <input id="rb13" type="radio" name="donate" :disabled="!(raritysites && marketplaces && this.iswallet)" value="0.5" v-model="donate">
                    <label class="noselect" for="rb13">0.5</label>
                    <input id="rb14" type="radio" name="donate" :disabled="!(raritysites && marketplaces && this.iswallet)" value="1" v-model="donate">
                    <label class="noselect" for="rb14">1.0</label>
                </div>
                <input type="hidden" name="rankingops" :value="rankingops">
                <input type="hidden" name="signature" :value="signature">
                <div class="search">
                    <button type="submit" :disabled='!(isCollection && marketplaces && raritysites && marketplace && raritysite && donate)'>search</button>
                </div>
            </form>
</template>

<script>
import { useWorkspace } from '@/composables'
import * as web3 from '@solana/web3.js';
import {defineComponent} from "vue";
import DropdownList from '@/components/DropdownList.vue'
import { createToast } from 'mosha-vue-toastify';
import axios from 'axios';

export default defineComponent ({
  name:"solanaForm",
  setup(){
    const {wallet, connection} = useWorkspace();
    return{
      sendTransaction: wallet.sendTransaction,
      publicKey: wallet.publicKey,
      connection,
      iswallet: wallet.connected,
    }
  },
  data() {
    return {
      isCollection: false,
      collection: "",
      marketplace: false,
      raritysite: false,
      signature: "",
      rarity: {},
      markets: {},
      loading: false,
      howrareis: false,
      raritymon: false,
      moonrank: false,
      marketplaces: false,
      raritysites: false,
      checked: false,
      solanart: false,
      alphaArt: false,
      magicEden: false,
      digitalEyes: false,
      marketops:false,
      rankingops:false,
      donate:false,
    }
  },
  components: {
    DropdownList
  },
  mounted () {
    this.getrarity()
  },
  methods: {
    getrarity() {
      this.laoding = true;
      axios.get("/getrarity").then( response => {
        this.rarity = response.data;
        this.loading=false;
        })
    },
    isMobile() {
      if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
        return true
      } else {
        return false
      }
    },
    updateData(val){
      if (val){
        if (val in this.rarity) {
          if ("defaultrare" in this.rarity[val]){
            this.raritysite = this.rarity[val]["defaultrare"]
          }
          if ("moonrank" in this.rarity[val]){
            this.moonrank = true;
          }
          if ("howrareis" in this.rarity[val]){
            this.howrareis = true;
          }
          if ("raritymon" in this.rarity[val]){
            this.raritymon = true;
          }
          this.rankingops = JSON.stringify(this.rarity[val]);
          this.raritysites = this.moonrank || this.howrareis || this.raritymon;
        }
        if ("solanart" in this.markets){
          this.solanart = true;
        }
        if ("alphaArt" in this.markets){
          this.alphaArt = true;
        }
        if ("magicEden" in this.markets){
          this.magicEden = true;
        }
        if ("digitalEyes" in this.markets){
          this.digitalEyes = true;
        }
        this.marketplaces = this.solanart || this.alphaArt || this.magicEden || this.digitalEyes;
        this.marketops = JSON.stringify(this.markets);
      }
      else{
        this.raritysite = false;
        this.marketplace = false;
        this.donate = false;
        this.moonrank = false;
        this.howrareis = false;
        this.raritymon = false;
        this.solanart = false;
        this.alphaArt = false;
        this.magicEden = false;
        this.digitalEyes = false;
        this.raritysites = this.moonrank || this.howrareis || this.raritymon;
        this.marketplaces = this.solanart || this.alphaArt || this.magicEden || this.digitalEyes;
        this.marketops = false;
        this.rankingops = false;
      }
    },
    // submit the form to our backend api
    async submitForm() {
      await (async () => {
        if (this.donate != "0") {
          // Connect to cluster
          var to = new web3.PublicKey('FqdxQD3t1CtECDPUDeB19mV26JzcTTfS8Hz8CBCpqLTS');
          // Add transfer instruction to transaction
          var transaction = new web3.Transaction().add(
              web3.SystemProgram.transfer({
              fromPubkey: this.publicKey,
              toPubkey: to,
              lamports: web3.LAMPORTS_PER_SOL * parseFloat(this.donate),
              }),
          );
          // Sign transaction, broadcast, and confirm
          try {
            createToast({title:"Confirm The Transaction..."}, {position:"bottom-left", showCloseButton:false})
            var signature = await this.sendTransaction(transaction, this.connection);
            createToast({title:"Transaction Successful!"}, {type: "success", position:"bottom-left", showCloseButton:false, showIcon:true})
            this.signature = signature;
            this.$refs.form.submit();
          }
          catch(err) {
            createToast({title: "Transaction Error", description: err.toString()}, {type: "danger", position:"bottom-left", showCloseButton:false, showIcon:true});
          }
        }
        else {
          this.signature = "free";
          this.$refs.form.submit();
        }
      })();
      if (this.isMobile() != true) {
        setTimeout(() => {  this.$emit("loading"); }, 10);
      }
    }
  },
  watch: {
      loading: function(val) {
        if (val === false){
          if(this.collection){
            this.updateData(this.collection)
          }
        }
      },
      collection: function(val) {this.updateData(val)}
    },
});
</script>